/* General Styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f7fc;
  }
  
  /* Patient Dashboard Container */
  .patient-dashboard-container {
    display: flex;
    height: 100vh;
    justify-content: space-between;
  }
  
  /* Sidebar Styles */
  .patient-sidebar {
    width: 280px;
    background-color: #a4bac5;
    color: #fff;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start; /* Align items at the top */
  }
  
  .sidebar-header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .sidebar-header h2 {
    margin: 0;
  }
  
  .sidebar-links {
    margin-bottom: 30px; /* Add more space from the top */
    flex-grow: 1; /* Push links to the center */
  }
  .patient-sidebar {
    width: 280px;
    background-color: #a4bac5;
    padding: 20px;
    border-right: 1px solid #ddd;
  }
  
  .sidebar-header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .sidebar-links button {
    display: flex;
    align-items: center;
    padding: 10px;
    width: 100%;
    background: none;
    border: none;
    color: #333;
    font-size: 1.1rem;
    cursor: pointer;
    margin: 10px 0;
  }
  
  .sidebar-links button i {
    margin-right: 10px;
    font-size: 1.3rem;
  }
  
  .sidebar-links button:hover {
    background-color: #ddd;
  }
  
  .logout-button {
    display: flex;
    align-items: center;
    background-color: red;
    color: white;
    font-size: 1.1rem;
    padding: 10px;
    width: 100%;
    cursor: pointer;
    margin-top: 20px;
  }
  
  .logout-button i {
    margin-right: 10px;
  }
  
  .sidebar-button {
    background-color: #2c3e50;
    color: white;
    border: none;
    padding: 12px;
    width: 100%;
    margin: 10px 0;
    text-align: left;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .sidebar-button:hover {
    background-color: #1abc9c;
  }
  
  .logout-button {
    background-color: #d9534f;
    color: white;
    padding: 12px;
    width: 100%;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .logout-button:hover {
    background-color: #c9302c;
  }
  
  /* Main Content */
  .patient-main-content {
    flex: 1;
    padding: 20px;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center; /* Center the content */
  }
  
  .patient-dashboard-header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .patient-dashboard-header h1 {
    font-size: 24px;
    color: #333;
  }
  
  /* Card Styles */
  .card {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
    width: 100%;
    max-width: 600px; /* Restrict the width of the card */
  }
  
  .profile-form input,
  .profile-form select,
  .profile-form textarea,
  .appointment-form input,
  .appointment-form select,
  .appointment-form textarea {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .profile-form button,
  .appointment-form button {
    background-color: #006a8f;
    color: white;
    padding: 12px 20px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .profile-form button:hover,
  .appointment-form button:hover {
    background-color: #00577a;
  }
  
  /* Profile Information */
  .profile-info p {
    font-size: 16px;
    margin: 10px 0;
  }
  
  .profile-actions button {
    background-color: #28a745;
    color: white;
    padding: 10px 20px;
    margin-right: 10px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .profile-actions button:hover {
    background-color: #218838;
  }
  
  .profile-actions .delete-profile {
    background-color: #dc3545;
  }
  
  .profile-actions .delete-profile:hover {
    background-color: #c82333;
  }
  
  /* Appointment History */
  .appointment-history ul {
    list-style-type: none;
    padding: 0;
  }
  
  .appointment-history li {
    background-color: #f9f9f9;
    margin: 10px 0;
    padding: 15px;
    border-radius: 5px;
    border-left: 5px solid #006a8f;
  }
  
  .appointment-history li strong {
    color: #333;
  }
  
  .appointment-history li p {
    font-size: 14px;
    margin: 5px 0;
  }
  
  /* Media Queries for Responsiveness */
  @media (max-width: 768px) {
    .patient-dashboard-container {
      flex-direction: column;
    }
  
    .patient-sidebar {
      width: 100%;
      height: auto;
    }
  
    .patient-main-content {
      padding: 10px;
    }
  
    .card {
      max-width: 100%; /* Make the cards take full width on small screens */
    }
  }
  
  @media (max-width: 480px) {
    .patient-dashboard-header h1 {
      font-size: 20px;
    }
  
    .sidebar-button {
      font-size: 14px;
    }
  
    .logout-button {
      font-size: 14px;
    }
  
    .profile-form input,
    .profile-form select,
    .profile-form textarea,
    .appointment-form input,
    .appointment-form select,
    .appointment-form textarea {
      font-size: 14px;
    }
  }
  